<template>
  <div>
        <img class="headimg" src="..\assets\2c933500885db5db33cc5094480e9ae8.jpeg" alt="">
        <cube-form
            :model="model"
            :schema="schema"
            @submit="submitHandler">
        </cube-form>
    </div>
</template>

<script>
export default {
    data() {
        return {
            model: {
                username: '',
                password: '',
            },
            schema: {
                fields: [
                    {   
                        //格式
                        type: 'input',
                        //对应上面的model
                        modelKey: 'username',
                        //input头显示
                        label: '用户名',
                        props: {
                            //input内提示
                            placeholder: '请输入用户名'
                        },
                        //规则
                        rules: {
                            //非空检测
                            required: true,
                            //数据类型
                            type: 'string',
                            //最小值
                            min: 3,
                            //最大值
                            max: 15,
                        },
                        //输入检测还是失焦检测
                        trigger: 'blur', //失焦检测
                        //不满足规则时提示
                        messages: {
                            required: '用户名不能为空',
                            min: '不能小于3个',
                            max: '不能大于15个',
                        }
                    },
                    {
                        type: 'input',
                        modelKey: 'password',
                        label: '密 码',
                        //props input样式
                        props: {
                            placeholder: '请输入密码',
                            type: 'password',
                            eye: {
                                open: false,
                            }
                        },
                        rules: {
                            required: true,
                            min: 6,
                            max: 20,
                        },
                        trigger: 'blur',
                        messages: {
                            required: '密码不能为空',
                            min: '密码不能小于6位',
                            max: '密码不能大于20位',
                        },
                    },
                    {
                        type: 'submit',
                        label: '注册',
                    }
                ]
            }
        }
    },
    methods: {
        submitHandler(e) {
            //e.preventDefault()这个东西可以阻止表单提交
            e.preventDefault()
            this.$http.get('/api/register', {params: this.model}).then(res => {
                console.log(res.message)
            }).catch(err => {
                console.log(err)
            })
        }
    }
}
</script>

<style lang="stylus">
    .headimg
        width 100%
        height 150px
</style>