import Vue from 'vue'
import './cube-ui'
import App from './App.vue'
import router from './router'
import store from './store'
//使用axios需要引入
//axios 是一个经过es6的pormise封装而成的ajax请求
import axios from 'axios'
import 'amfe-flexible'
import setAxios from './setaxios'

//暴露出来的接口是实例 需要执行一下
setAxios()

Vue.config.productionTip = false
//可以把axios挂载在vue的原型prototype上
Vue.prototype.$http = axios

//路由守卫
//beforeEach为路由的前置钩子，无论是刷新还是跳转路由，都是先进入路由前置钩子
router.beforeEach((to, from, next) => {
//可以获取本地localStorage的token数据 来判断是否登录
  store.commit('settoken', localStorage.getItem('token'))
  if(to.meta.requireAuth) { //如果路由守卫meta里的requireAuth是否为true
    if(store.state.token) { //判断token是否存在 如果存在的话就跳转过去 如果不存在就跳转到登录页面
      next()
    } else {
      next({ 
        path: '/login', 
        query: { redirect: to.fullPath } //跳转到登录页面时带上目前的页面地址  如果登录成功可以重定向到这个页面
      })
    }
  } else {
    next()
  }

})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
