import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

let store =  new Vuex.Store({
  //变量存储库，token存在这里
  state: {
    token: '',
    //存储购物车商品的变量数组
    //JSON.parse调用本地存储里的数据
    arrMall: JSON.parse(localStorage.getItem('arrMall')) || [],
  },
  //同步方法  第一个参数都是同步变量存储库state
  mutations: {
    //设置vuex的token
    settoken(state, token) {
      state.token = token
    },
    //添加商品到购物车方法
    toMall(state, tag) {
      let goods = state.arrMall.find(v => tag.label == v.title)
      if(goods) {
        goods.mallCount += 1
      } else {
        state.arrMall.push({ title:tag.label, mallCount: 1})
      }
    },
    //购物车数量加1
    addMall(state, index) {
      state.arrMall[index].mallCount++
    },
    //购物车数量减1
    removeMall(state, index) {
      if(state.arrMall[index].mallCount > 1) {
        state.arrMall[index].mallCount--
      } else {
        if(window.confirm('是否从购物车移除?')) {
          state.arrMall.splice(index, 1)
        }
      }
    },
    //清空购物车数据
    clearMall(state) {
      state.arrMall = []  //这里清空数组是[]  ''不能清空
    }
  },
  //异步方法
  actions: {
  },
  //相当于vue里的计算属性 component
  getters:{
    countSum: state => {
      let num = 0
      state.arrMall.forEach(v => {
        num += v.mallCount
      })
      return num
    }
  },
  modules: {
  }
})

//每次调用mutations的时候，都会进入这个方法，我们可以在这里做一些自己的处理
store.subscribe((mutations, state) => {
  //这里可以把mutations里的数据存在本地存储里，可以在上面arrMall里调用本地存储
  localStorage.setItem('arrMall', JSON.stringify(state.arrMall))
})

export default store